import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { FProgressType, FProgressStatusType, FProgressFormatter, FProgressGapPositionType, FProgressCirclePath, FProgressStrokeColorType, FProgressColorGradient, FProgressGradientProgress, FProgressStrokeLinecapType } from './progress-options';
import { handleLinearGradient, handleCircleGradient } from './progress-utils';

const defaultFormatter: FProgressFormatter = (p: number): string => `${p}%`;
let gradientIdSeed = 0;
const statusColorMap = new Map([
  ['normal', '#388fff'],
  ['exception', '#f24645'],
  ['success', '#51bd78']
]);

@Component({
  selector: 'farris-progress',
  templateUrl:'./progress.component.html',
  styleUrls:[
    './progress.component.scss'
  ]
})
export class ProgressComponent implements OnInit, OnChanges{
  //è¿åº¦æ¡ç±»å
  @Input() type:FProgressType = 'line';
  //è¿åº¦æ¡å®½åº¦
  @Input() strokeWidth:number;
  //è¿åº¦æ¡å°ºå¯¸ 'default' | 'small'
  @Input() size = 'default';
  //æ¯å¦æ¾ç¤ºè¿åº¦æ¡ä¿¡æ¯
  @Input() showInfo:boolean = true;
  //è¿åº¦æ¡ç¶æ é»è®¤'normal'
  @Input() status:FProgressStatusType;
  //è¿åº¦æ¡ä¿¡æ¯æ ¼å¼å
  @Input() format?: FProgressFormatter;
  //å·²å®æçåæ®µç¾åæ¯
  @Input() successPercent: number;
  //è¿åº¦æ¡é¢è²
  @Input() strokeColor:FProgressStrokeColorType;
  //åå½¢è¿åº¦æ¡å®½åº¦
  @Input() width:number;
  //ä»ªè¡¨çç¼ºå£ä½ç½®
  @Input() gapPosition:FProgressGapPositionType = 'top';
  //ä»ªè¡¨çç¼ºå£è§åº¦ é»è®¤æ¯75
  @Input() gapDegree:number;//ä»ªè¡¨çä¸è§åº¦ 0-360åº¦
  //è¿åº¦æ¡ç­ç¹å½¢ç¶ é»è®¤åè§ å¯éç´è§square
  @Input() strokeLinecap:FProgressStrokeLinecapType = 'round';
  //è¿åº¦æ¡ç¾åæ¯
  _percent:number = 0;
  @Input() 
  get percent(){
    return this._percent;
  }
  set percent(percent){
    this._percent = percent;
  }
  //å­å¨çç¶æ
  private cachedStatus: FProgressStatusType = 'normal';
  //è®¡ç®çç¶æ
  private inferredStatus: FProgressStatusType = 'normal';

  pathString:string;
  trailPathStyle: any;
  //åç¯è·¯å¾
  progressCirclePath:FProgressCirclePath[] = [];
  //æ¯å¦æ¸å
  isGradient = false;
  gradientId = gradientIdSeed++;
  lineGradient: string | null = null;
  circleGradient: Array<{ offset: string; color: string }>;
  //è·å¾è¿åº¦æ¡ç¶æ
  get progressStatus():FProgressStatusType{
    return this.status || this.inferredStatus;
  }
  //è¿åº¦æ¡ä¿¡æ¯æ ¼å¼å
  get progressFormatter(): FProgressFormatter {
    return this.format || defaultFormatter;
  }

  //è®¡ç®è¿åº¦æ¡å®½åº¦ é»è®¤8px smallä¸º6px
  get strokeW(): number {
    return this.strokeWidth || (this.type === 'line' && this.size !== 'small' ? 8 : 6);
  }
  //å¤æ­æ¯å¦ä¸ºç¯å½¢è¿åº¦æ¡
  get isCircleStyle(): boolean {
    return this.type === 'circle' || this.type === 'dashboard';
  }

  trackByFn = (index: number) => `${index}`;
  constructor() { }

  ngOnInit() {
    //åç¯å®½åº¦å¤æ­
    if(this.type === 'circle' || this.type === 'dashboard'){
      this.width = this.width || (this.size !== 'small' ? 80 : 50);
    }

    this.setStrokeColor();
    this.getCirclePaths();
  }

  ngOnChanges(changes: SimpleChanges): void {
    const { gapPosition, strokeLinecap, strokeColor, gapDegree, type, status, percent, successPercent } = changes;
    //ç¶æåå
    if (status) {
      this.cachedStatus = this.status || this.cachedStatus;
    }
    //è¿åº¦ å·²å®æçåæ®µç¾åæ¯ åå
    if (percent || successPercent) {
      const fillAll = parseInt(this.percent.toString(), 10) >= 100;
      if (fillAll) {
        if ((this.isNotNil(this.successPercent) && this.successPercent! >= 100) || this.successPercent === undefined) {
          this.inferredStatus = 'success';
          // this.status = this.inferredStatus;
        }
      } else {
        this.inferredStatus = this.cachedStatus;
      }
      this.status = this.inferredStatus;
    }
    //è¾¹çº¿é¢è²åå
    if (strokeColor) {
      this.setStrokeColor();
    }
    //åç¯ä¸äºå±æ§åå
    if (gapPosition || strokeLinecap || gapDegree || type || percent || strokeColor) {
      this.getCirclePaths();
    }
  }

  private getCirclePaths(): void {
    if (!this.isCircleStyle) {
      return;
    }
    const values = this.isNotNil(this.successPercent) ? [this.successPercent!, this.percent] : [this.percent];
    const radius = 50 - this.strokeW / 2;
    const gapPosition = this.gapPosition || (this.type === 'circle' ? 'top' : 'bottom');
    const len = Math.PI * 2 * radius;
    const gapDegree = this.gapDegree || (this.type === 'circle' ? 0 : 75);

    let beginPositionX = 0;
    let beginPositionY = -radius;
    let endPositionX = 0;
    let endPositionY = radius * -2;

    switch (gapPosition) {
      case 'left':
        beginPositionX = -radius;
        beginPositionY = 0;
        endPositionX = radius * 2;
        endPositionY = 0;
        break;
      case 'right':
        beginPositionX = radius;
        beginPositionY = 0;
        endPositionX = radius * -2;
        endPositionY = 0;
        break;
      case 'bottom':
        beginPositionY = radius;
        endPositionY = radius * 2;
        break;
      default:
    }

    this.pathString = `M 50,50 m ${beginPositionX},${beginPositionY}
       a ${radius},${radius} 0 1 1 ${endPositionX},${-endPositionY}
       a ${radius},${radius} 0 1 1 ${-endPositionX},${endPositionY}`;

    this.trailPathStyle = {
      strokeDasharray: `${len - gapDegree}px ${len}px`,
      strokeDashoffset: `-${gapDegree / 2}px`,
      transition: 'stroke-dashoffset .3s ease 0s, stroke-dasharray .3s ease 0s, stroke .3s'
    };

    // Calculate styles for each path.
    this.progressCirclePath = values
      .map((value, index) => {
        const isSuccessPercent = values.length === 2 && index === 0;
        return {
          stroke: this.isGradient && !isSuccessPercent ? `url(#gradient-${this.gradientId})` : null,
          strokePathStyle: {
            stroke: !this.isGradient ? (isSuccessPercent ? statusColorMap.get('success') : (this.strokeColor as string)) : null,
            transition: 'stroke-dashoffset .3s ease 0s, stroke-dasharray .3s ease 0s, stroke .3s, stroke-width .06s ease .3s',
            strokeDasharray: `${((value || 0) / 100) * (len - gapDegree)}px ${len}px`,
            strokeDashoffset: `-${gapDegree / 2}px`
          }
        };
      })
      .reverse();
  }

  private setStrokeColor(): void {
    const color = this.strokeColor;
    //å¤æ­æ¯å¦ä¸ºæ¸å
    const isGradient = (this.isGradient = !!color && typeof color !== 'string');
    //çº¿æ§æ¸å
    if (isGradient && !this.isCircleStyle) {
      this.lineGradient = handleLinearGradient(color as FProgressColorGradient);
    }
    //åç¯æ¸å
    else if (isGradient && this.isCircleStyle) {
      this.circleGradient = handleCircleGradient(this.strokeColor as FProgressGradientProgress);
    }
    //ä¸æ¯æ¸å
    else {
      this.lineGradient = null;
      this.circleGradient = [];
    }
  }

  isNotNil<T>(value: T): value is NonNullable<T> {
    return typeof value !== 'undefined' && value !== null;
  }

}
